import React from "react";
import "./smallCompanyBlock.css";
import { Link } from "react-router-dom";

const SmallCompanyBlock = ({ item }) => {
  return (
    <Link
      to={{ pathname: "/company", state: item }}
      className="SmallCompanyBlock"
    >
      <div className="imgContainer">
        <img
          alt="Иконка заведения"
          src="https://static.relax.by/images/common/catalog/catalog_promo_block_images/6d0223deda15cc3d87448ac670decd73.png"
          className="companyImg"
        />
        <div className="gradientBlock">
          <p className="gradientBlockName">{item.company_name}</p>
          <p className="gradientBlockType">{item.company_type}</p>
        </div>
      </div>
      <div className="description">
        <div className="topBlockText">
          <div className="companyNameWrap">
            <p className="companyName">{item.company_name}</p>
            <p className="companyType">{item.company_type}</p>
          </div>
          <p className="companyTitle">{item.party_type}”</p>
        </div>
        <div className="bottomBlockText">
          <div className="row">
            <img alt="eye" src={"./img/eye.png"} className="eye" />
            <p className="leftText">75862</p>
            <span className="circle"> </span>
            <p className="leftText">387</p>
          </div>
          <p className="rightText">{item.work_time.join(" - ")}</p>
        </div>
      </div>
      <div className="descriptionMobile">
        <p className="endTime">
          Работает до <span>05.00</span>
        </p>
        <p className="distance">
          10,87 <span>км.</span>
        </p>
      </div>
    </Link>
  );
};

export default SmallCompanyBlock;
